@import "./../../Utils/colors.scss";

.ak__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 3rem;
  width: 90%;
  margin: 0% auto;
  border-radius: 1rem;
  margin-bottom: 70px;
  margin-top: 90px;
  background: linear-gradient(
    45.22deg,
    rebeccapurple -119.55%,
    #011235 23.86%,
    rebeccapurple 129.55%
  );

  @media (max-width: 554px) {
    flex-direction: column;
    padding: 2rem;
  }
}

.ak__cta-content {
  display: flex;
  flex-direction: column;
  text-align: left;
  color: #fff;
}

.ak__cta-content p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 30px;
  font-weight: 500;
}

.ak__cta-content h3 {
  font-family: "Manrope", sans-serif;
  font-size: 32px;
  line-height: 45px;
  font-weight: 800;
  font-family: "Teko", sans-serif;

  @media (max-width: 554px) {
    font-size: 20px;
    line-height: 30px;
  }
}

.ak__cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.ak__cta-btn button {
  background: $primaryColor;
  color: #fff;
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  padding: 0.5rem 1rem;
  border-radius: 2rem;

  border: none;
  outline: none;
  cursor: pointer;
  min-width: 150px;
}

@media screen and (max-width: 650px) {
  .ak__cta {
    flex-direction: column;
  }

  .ak__cta-btn {
    margin: 2rem 0 0;
  }
}

@media screen and (max-width: 550px) {
  .ak__cta {
    flex-direction: column;
  }

  .ak__cta-content h3 {
    font-size: 18px;
    line-height: 32px;
  }

  .ak__cta-btn button {
    font-size: 14px;
    line-height: 28px;
  }
}
